#page {
    overflow-x: hidden;
}

#wrapper-navbar + .wrapper {
    padding: 0;
    
    #main {
        padding-bottom: 10rem;
    }
}

#search-wrapper,
#error-404-wrapper {
    padding-top: 10rem;

    .page-header {
        margin-bottom: $spacer * 2;
    }
}

#page-wrapper {
    padding-bottom: 0;
}

