body.understrap-no-sidebar #main {

    @include media-breakpoint-up(sm) {
        position: relative;
        // border-left: 1px solid $gray-100;
        // border-right: 1px solid $gray-100;
        padding-left: 4.16%;
        padding-right: 4.16%;

        &:before,
        &:after {
            content: "";
            display: block;
            position: absolute;
            z-index: -100;
            width: 1px;
            left: 0;
            top: 0;
            bottom: 0;
            background-color: rgba($dark, .1);

        }

        &:after {
            left: unset;
            right: 0;
        }
    }

    @include media-breakpoint-up(lg) {
        padding-left: 8.3333333333%;
        padding-right: 8.3333333333%;
    }

}


.stretch-linked-block {
    position: relative !important;
    transition: $transition-fade;

    .editorskit-block-link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 90;
    }

    &:hover {
        opacity: .7;

        .wp-block-button.is-style-arrow-link .wp-block-button__link:after {
            right: 0;
        }
    }
}

.stretched-link {
    color: $body-color;

    &:hover {
        text-decoration: none;
        color: $body-color;
    }
}

.entry-footer {
    position: relative;
    z-index: 2;
}

.filler-line {
    display: inline-block;
    height: 1px;
    background-color: $primary-40;
    flex-grow: 1;
    margin-left: $spacer;
}